@font-face{
  font-family:'Onest';
  src:url('https://static.tildacdn.com/tild6361-3234-4463-a165-303032646263/OnestRegular1602-hin.woff') format('woff');
  font-weight:400;
  font-style:normal;
}

.App {
  text-align: center;
  background-image: url(../src/background.jpg);
  background-repeat: no-repeat;
  background-size: cover;
}

.App-logo {
  padding-top: 20vmin;
  height: 20vmin;
  opacity: 40%;
}

.App-header {
  background-color: #282c34;
  opacity: 85%;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
  font-size: calc(10px + 2vmin);
  color: azure;
}

.App-link {
  color: azure;
}

.link {
  color: aquamarine;
}

.Main-App-link {
  color: #B1BBC6;
  text-decoration: none;
  font-family:'Onest',Arial,sans-serif;
  font-weight:400;
  font-size: 25px;
}

.Main-App-link:hover {
  color: #759AC4;
  text-decoration: none;
  transition: 1s;
}


ul {
  display: flex;
  flex-direction: row;
  justify-content: center;

}

li {
  list-style: none;
  padding: 0px 30px;
  margin: 5px 12px;
}

.App-content {
  color: silver;
  font-family:'Onest',Arial,sans-serif;
  font-weight:400;
  font-size: 16px;
  text-align: justify-all;
  width: 60%;
  margin: auto;
}

span {
  font-size: 25px;
}